.social-links-container {
  display: none;
}

@media (min-width: 1024px) {
  .social-links-container {
    display: flex;
    flex-direction: column;
    top: 40%;
    left: 0;
    position: fixed;
  }
}

ul {
  list-style: none;
}

.social-link-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 10rem;
  height: 2.75rem;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  margin-left: -7.5rem;
  transition-duration: 300ms;
  background-color: #bdbdbd;
  background-image: linear-gradient(to right, #1f2075, #81bc01);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}

.social-link-item:hover {
  margin-left: -0.25rem;
  border-radius: 0.375rem;
}

.rounded-tr-md {
  border-top-right-radius: 0.375rem;
}

.rounded-br-md {
  border-bottom-right-radius: 0.375rem;
}

.social-link-text {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  color: black;
}

.linkedin-icon {
  color: #2c96c7; /* Custom LinkedIn color */
}

.github-icon {
  color: #3b5998; /* Custom GitHub color */
}

.mail-icon {
  color: #ff59ba; /* Custom Mail color */
}

.facebook-icon {
  color: #1877f2; /* Custom Facebook color */
}

.instagram-icon {
  color: #c32aa3; /* Custom Instagram color */
}
